import {
	reqVideoList
} from '@/network/projectReq.js'
export default {
	name: "VideoPart",
	data() {
		return {
			reqParams: {
				id: 0,
				class_id: 0,
				page: 1,
				num: 10,

			},
			videoList: []
		}
	},
	props: {
		params:{
			default(){
				return {}
			},
			type:Object
		}
	},
	created() {
		
	},
	watch:{
		params(newVal){
			if(newVal !={}){
				this.reqParams.id = newVal.id
				this.reqParams.class_id = newVal.class_id
				this.getVideoList(this.reqParams)
			}
		}
	},
	methods: {
		getVideoList(params) {
			reqVideoList(params).then(res => {
				this.videoList = res.data.data
			})
		}
	}
}
